html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

header {
  width: 100%;
  height: 55px;
  background-color: #f1f1f1;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 2px 5px rgba(0,0,0,0.1);
  z-index: 1000;
}

.container {
  max-width: 430px;
  width: 100%;
  padding-top: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-wrap {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
